.hero {
  padding-top: 270px;
  padding-bottom: 40px;
  min-height: 900px;
  height: 100vh;
  background-image: url("../img/hero/hero-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__title {
  margin: 0;
  margin-bottom: 30px;
  max-width: 936px;
  font-size: 96px;
  line-height: 116px;
  color: var(--color-light);
}
.hero__descr {
  margin: 0;
  margin-bottom: 50px;
  font-weight: 400;
  font-size: 16px;
  color: var(--color-light);
  line-height: 150%;
}
