.footer {
  padding-top: 90px;
  padding-bottom: 150px;
  background-color: var(--color-darkgray);
}
.footer__logo {
  display: inline-block;
  margin-bottom: 48px;
  color: var(--color-light);
  font-size: 32px;
  line-height: 130%;
  font-weight: 500;
}
.footer__address {
  margin-bottom: 30px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--color-gray);
}
.social {
  display: flex;
  align-items: center;
}
.social__item:not(:last-child) {
  margin-right: 20px;
}
.social__link {
  display: block;
  width: 40px;
  height: 40px;
}
.footer__title {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 32px;
  line-height: 130%;
  color: var(--color-light);
}
.footer__form {
  position: relative;
  width: 100%;
}
.footer__input {
  border: none;
  border-bottom: 1px solid var(--color-gray);
  padding: 24px 0;
  padding-right: 30px;
  width: 100%;
  font-weight: 700;
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: var(--color-light);
  background-color: transparent;
  outline: none;
}
.footer__btn {
  position: absolute;
  right: 0;
  bottom: 24px;
  width: 24px;
  height: 24px;
}
