.nav__list {
  @include flex;
}
.nav__item:not(:last-child) {
  margin-right: 35px;
}
.nav__link {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: var(--color-light);
  transition: 0.3s all ease-in-out;
}
.nav__link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  width: 0;
  margin: 0;
  height: 1px;
  color: transparent;
  background-color: var(--color-light);
  transform-origin: left;
  transition: all 0.3s ease-in-out;
}
.nav__link:hover {
  color: var(--color-light);
}
.nav__link:hover::after {
  width: 100%;
}
