.services {
  padding-bottom: var(--offset);
}
.services__image {
  width: 100%;
  height: 900px;
  margin-bottom: var(--offset);
  object-fit: cover;
}
.services-item {
  border-top: 1px solid rgba(29, 29, 31, 0.4);
  padding: 40px 0;
}
.services-item__col {
}
.services-item__title {
  margin: 0;
  font-weight: 500;
  font-size: 32px;
  line-height: 130%;
  color: var(--color-darkgray);
}
.services-item__descr {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--color-gray);
}
