.about__top {
  padding-top: var(--offset);
  margin-bottom: var(--offset);
}
.about__item:not(:last-child) {
  margin-bottom: 60px;
}
.about__title {
  margin: 0;
  margin-bottom: 60px;
  font-weight: 700;
  font-size: 44px;
  line-height: 140%;
  color: var(--color-darkgray);
}
.about__descr {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--color-gray);
}
.about__descr:not(:last-child) {
  margin-bottom: 30px;
}
.completed__value {
  font-weight: 300;
  font-size: 64px;
  line-height: 140%;
  color: var(--color-darkgray);
}
.completed__descr {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--color-gray);
}
.about__video {
  position: relative;
  width: 100%;
  height: 630px;
  overflow: hidden;
}
.about__video video {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about__play {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100px;
  height: 100px;
  transform: translate(-50%, -50%);
  color: var(--color-light);
  transition: color 0.3s ease-in-out;
}
.about__play svg {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about__play circle {
  fill: currentColor;
}
.about__play--hidden {
  display: none;
}
