.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 40px 0;
}
.header__container {
  @include flex-all-sb;
}
.header__right {
  @include flex-v-center;
}
.header__search {
  margin-right: 35px;
  width: 24px;
  height: 24px;
  color: var(--color-light);
  transition: color 0.3s ease-in-out;
}
.header__search svg {
  display: block;
  width: 100%;
  height: 100%;
  stroke: currentColor;
  object-fit: cover;
}
