.quote {
  padding-bottom: var(--offset);
}
.quote__title {
  margin: 0;
  margin-bottom: 50px;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  color: var(--color-gray);
}
.quote__content {
  margin: 0;
  max-width: 820px;
}
.quote__text {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 36px;
  line-height: 150%;
  color: var(--color-darkgray);
}
.quote__author {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--color-darkgray);
}
.quote__author span {
  color: var(--color-gray);
}
